@import '../../../styles/variables';

.friends-bar {
  position: fixed;
  right: 0;
  top: 60px;
  width: 15%;
  box-sizing: border-box;
  background-color: $dark-gray;
  height: calc(100vh - 122px);
  overflow-y: scroll;
  padding-bottom: 15px;

  &__list {
    padding: 0;
    margin: 0 0 17px;

    list-style-type: none; }

  .text-button {
    margin: 0 auto; } }

@media screen and ( max-width: 1400px ) {
  .friends-bar {
    display: none; } }
