@import '../../../../styles/variables';

.artist-card {

  &__image {
    height: 200px;
    position: relative;

    background-size: cover;
    background-position: center;

    &:hover .artist-card__play {
      opacity: 1; } }

  &__play {
    position: absolute;
    top: 50px;
    width: 60px;
    height: 100px;
    left: 50%;
    transform: translateX(-50%);

    transition: opacity 0.5s;
    opacity: 0;
    display: block;
    fill: $white;
    cursor: pointer; }

  &__name {
    padding: 15px;
    color: $white;
    text-decoration: none;

    display: block;
    background-color: $mainTheme; }

  &__name:hover {
    text-decoration: underline; } }
