@import '../../../../styles/variables';

.track-cover {
  position: relative;
  bottom: 2px;
  width: 100%;
  padding-right: 10px;

  display: flex;
  align-items: center;
  box-sizing: border-box;
  background-color: $mainTheme;
  border-top: 1px solid $dark-gray;
  border-bottom: 1px solid $dark-gray;

  &__text {
    padding: 3px 15px;
    height: 100%;
    box-sizing: border-box; }

  &__name {
    display: block;
    margin: 6px 0;

    color: inherit;
    text-decoration: none;

    &:hover {
      color: $white;
      text-decoration: underline; } }

  .added-icon {
    margin: 0 0 0 auto; }

  &__image {
    width: 50px; } }

@media screen and ( max-width: 770px ) {
  .track-cover {
    display: none; } }
