@import '../../../../styles/variables';

@mixin flex() {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.track-controls {
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  padding: 5px 15px;
  width: 100%;
  height: 62px;

  text-transform: uppercase;
  font-size: 11px;

  @include flex();
  background-color: $mainTheme;

  &__actions {
    @include flex();
    width: 6%; }

  &__play {
    width: 30px;
    height: 30px;
    fill: $hoverFontColor;
    cursor: pointer; }

  &__skip {
    fill: $hoverFontColor;
    width: 25px;
    height: 25px;
    cursor: pointer; }

  &__skip:hover,
  &__play:hover {
    fill: $white; }

  &__progress {
    @include flex();
    width: 70%;
    height: 16px;
    padding: 0 30px;
    box-sizing: border-box;

    &-range {
      width: calc(100% - 70px); }

    &:hover .noUi-handle {
      opacity: 1; }

    .noUi-handle:focus {
      outline: none; }

    .noUi-connect {
      background-color: $green; } }

  .noUi-target {
    height: 4px;
    border: none;
    border-radius: 0; }

  .noUi-handle {
    width: 16px;
    height: 16px;
    right: -15px;
    opacity: 0;

    background-color: $hoverFontColor;
    box-shadow: none;
    border: none;
    border-radius: 50%; }

  .noUi-base {
    background-color: $gray; }

  .noUi-handle-lower::before,
  .noUi-handle-lower::after {
    display: none; }

  .noUi-touch-area {
    width: 18px;
    height: 18px; }

  &__tools {
    @include flex();
    width: 25%; }

  &__menu,
  &__shuffle,
  &__refresh,
  &__phone,
  &__valume-icon {
    fill: $light-gray;
    height: 18px;
    width: 18px;
    cursor: pointer; }

  &__menu:hover,
  &__shuffle:hover,
  &__refresh:hover,
  &__phone:hover {
    fill: $hoverFontColor; }

  &__menu {
    height: 15px;
    width: 15px; }

  &__phone {
    width: 12px;
    height: 12px;
    margin-top: -2px; }

  &__device {
    @include flex();
    width: 120px; }

  &__lyrics,
  &__device {
    cursor: pointer; }

  &__lyrics:hover, {
    text-decoration: underline;
    color: $hoverFontColor; }

  &__device:hover {
    color: $hoverFontColor; }


  &__valume {
    @include flex();
    width: 76px;
    padding-right: 15px;

    &-icon {
      width: 24px;
      height: 24px; }

    &-range {
      width: 50px; }

    .noUi-connect {
      background-color: $hoverFontColor; }

    &-range:hover {

      .noUi-handle {
        opacity: 1; } } } }

@media screen and ( max-width: 1400px ) {
  .track-controls {

    &__actions {
      width: 10%; }

    &__progress {
      width: 50%; }

    &__tools {
      width: 40%; } } }

@media screen and ( max-width: 980px ) {
  .track-controls {

    &__progress {
      width: 40%; }

    &__tools {
      width: 50%; } } }

@media screen and ( max-width: 770px ) {
  .track-controls {
    display: block;
    padding: 15px 15px 10px;
    height: auto;

    &__actions {
      width: 25%;
      float: left; }

    &__progress {
      width: 75%;
      height: 30px; }

    &__tools {
      width: 100%;
      margin-top: 5px; }

    &__valume {
      width: 20%;

      &-range {
        width: 80%; } } } }

@media screen and ( max-width: 480px ) {
  .track-controls {

    &__actions {
      width: 100%;
      margin: 5px 0 10px;
      justify-content: space-around; }

    &__progress {
      width: 100%;
      padding: 0; } } }
